<template>
    <div :newSetFolderDialog="newSetFolderDialog">
        <div class="divdialog" v-show="showMask">
            
            <el-input v-model="folderName" placeholder=""></el-input>

            <br/>
            <input type="text" v-model="folderIcon"/>
            <br/>
            <input type="text" v-model="folderColor"/>
            <br/>
            <button @click="createFolder"></button>
            <button @click="closeMask"></button>
        </div>
        
    </div>
</template>

<script>
export default {
    name: 'SetFolderDialog',
    data(){
        return{
            showMask: "",
            folderName: "",
            folderIcon: "",
            folderColor: ""
        }
    },
    methods: {
        getShowMask(){
            this.showMask = this.$store.getters.getShowMask;  
        },
        closeMask(){
            this.$store.commit('changeShowMask')
            this.getShowMask()
        },
        createFolder(){
            // console.log(this.folderName +","+ this.folderIcon +","+ this.folderColor)
            this.showMask = {
                id: '', //
                istop: true==true?'':'isnottop', 
                    islike: true==true?'':'isnotlike', 
                    isnew: true==true?'':'isnotnew', 
                    name: this.folderName, 
                    icon: '',
                    iconbg: this.folderColor, //"'"+this.folderColor+"'",
                    formdata: 0
            }
            this.$emit('childSend', this.showMask)//
            this.folderName = "";
            this.folderIcon = "";
            this.folderColor = "";
            this.closeMask();

            // {  
            //         istop: true==true?'':'isnottop', 
            //         islike: true==true?'':'isnotlike', 
            //         isnew: true==true?'':'isnotnew', 
            //         name: '', 
            //         icon: '',
            //         iconbg: '22,67,90',
            //         formdata: 80
            //     }
        }
    },
    mounted(){
        this.getShowMask()
    },
    computed:{
        newSetFolderDialog(){
            this.getShowMask()
        }
    }
}
</script>

<style scoped>
.divdialog{
    background: rgba(109, 97, 74, 0.884);
    border: 1px solid rgba(26, 21, 21, 0.534);

    width: 40vw;
    height: 50vh;
    border-radius: 10px;

    position: absolute;
    top: 25vh;
    left: 25vw;
}
</style>