<template>
    <div class="OptionC">
        <div v-for="(item,index) in info" :key="index">
            <!-- <button style="cursor: move;"></button> -->
            <!--radio onclick="if(this.c==1){this.c=0;this.checked=0}else this.c=1" -->
            <!-- js -->
            <input :type="item.type" name="a" onclick="if(this.c==1){this.c=0;this.checked=0}else this.c=1" @click="change(item)"/>
            <div class="optionSpan">
                <!-- <button></button> -->
                <input placeholder="" v-model="item.text"/>
            </div>
            <button @click="delChoose(index)"></button>
        </div>
        <button @click="addChoose"></button>
    </div>
</template>

<script>
export default {
    name: 'OptionC',
    props: {
        info: {
                type: Array,
                default() {
                    return [
                        {position: 1, isClick: false, isShow: false, text: "vv", isDel: false},
                        ]
                }
            }
    },
    methods: {
        change(item){
            item.isClick=!item.isClick;
            // this.info.
        },
        addChoose(){
            this.info.push({type: "radio", isClick: false, text: "" })
        },
        delChoose(index){
            this.info.splice(index,1) 
        }
    }
}
</script>
<style scoped>
.optionSpan{
    display: inline;
    position: relative;
}
.optionSpan button{
    display: inline;
    position: absolute;
    top: 0px;
    left: 0 px;
}
.optionSpan input{
    /* padding-left: 50px;  */
    /* padding-left: 50px; */
    width: 5vw;
}

</style>