<template>
    <div class="mainDel">
        <el-button @click="delForms"></el-button>
        <el-table :data="formList" stripe style="width: 100%">
            <el-table-column prop="publishDate" label="" width="180">
            </el-table-column>
            <el-table-column prop="name" label="" width="180">
            </el-table-column>
            <el-table-column prop="id" label="" width="180">
              <template slot-scope="scope">
                  <el-button @click="delForm(scope.row)"></el-button>
              </template>
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
import moment from 'moment'
export default {
    inject: ['reload'],
    name: 'MainDel',
    data() {
      return {
        formList: [],
      }
    },
    methods: {
        successNotice(mess) {
            this.$message({
            message: mess,
            type: 'success',
            center: true,
            duration: 1000
            });
        },
        selectFormsByCondition(){
            this.$qApi.getSelectFormsByCondition({
                userId: 20,
                condition: 'isDelete'
            }) 
            .then(successResponse =>{
                this.formList = successResponse.data;
            })
        },
        delForm(num){
            this.$qApi.getUpdFormToChar({
                formId: num.id,
                condition: 'isDelete'
            }) 
            .then(successResponse =>{ 
                this.successNotice("");
                this.reload()
            })
        },
        delForms(){
            this.$qApi.getDelForms({userId: 20}) 
            .then(successResponse =>{ 
                this.successNotice("") 
                this.reload()
            })
        }
    },
    mounted(){
        this.selectFormsByCondition();
    }
}
</script>

<style scoped>
.mainDel{
    width: 100%;
    height: 100%;
}
</style>


