<template>
  <div class="dataTable">
    <div>
      <el-button title="">
        <i class="el-icon-refresh-right"></i>
      </el-button>
      <el-button @click="exportExcel"></el-button>
    </div>
    <el-table id="tabledata" :data="tableData" border tooltip-effect="dark" fit>
      <!-- @selection-change="handleSelectionChange" ref="multipleTable"-->
      <el-table-column type="selection" style="width:0.1vmin, width:5vmax"></el-table-column>
      <el-table-column prop="tQuestionData" label=""></el-table-column>
      <el-table-column prop="submitter" label=""></el-table-column>
      <el-table-column prop="submit_time" label=""></el-table-column>
      <el-table-column prop="write_time" label=""></el-table-column>
      <el-table-column prop="write_place" label=""></el-table-column>
      <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
    </el-table>
  </div>
</template>

<script>
import FileSaver from 'file-saver'
import XLSX from 'xlsx'
export default {
  name: "dataTable",
  components: {},
  data() {
    return {
      tableData: [],
      formQuestion: [],
      multipleSelection: [],
    };
  },

  methods: {
    toggleSelection(rows) {
      if (rows) {
        rows.forEach((row) => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    selectDatas(formId) {
      this.$qDApi.getSelectDatas({ formId: formId }).then((successResponse) => {
        this.tableData = successResponse.data;
        this.formQuestion = this.$store.getters.getFormQuestiona;
      });
    },
    exportExcel () {
      /* generate workbook object from table */
      var wb = XLSX.utils.table_to_book(document.querySelector('#tabledata'))
      /* get binary string as output */
      var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
      try {
        FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), '.xlsx')
      } catch (e) { if (typeof console !== 'undefined') console.log(e, wbout) }
      return wbout
      },
  },
  mounted() {
    this.selectDatas(this.$store.getters.getOneForm.id);
  },
};
</script>

<style>
.dataTable {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  /* overflow-y: scroll; autobody */
}
</style>